import React, { ReactNode, HTMLAttributes } from 'react';
import { createComponent } from '../lib';
import type { JSX } from '@vime/core/dist/types';

import { 
  VmTooltip 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-tooltip', VmTooltip);
  

export interface TooltipProps extends JSX.VmTooltip, HTMLAttributes<HTMLVmTooltipElement> {
  children?: ReactNode | ReactNode[]
};

export default createComponent<HTMLVmTooltipElement, TooltipProps>(
  'vm-tooltip',
  new Set(['hidden','active','position','direction','isTouch','isMobile']),
);
  